.active-navbar {
  display: none;
}
.marker {
  position: absolute;
  top: 0;
  transition: 0.5s;
  z-index: 1;
}

.marker::before {
  content: '';
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 70px;
  height: 30px;
  background-color: red;
}
.navbar {
  background-color: darken($color: $background-primary, $amount: 15);
  // background-color: $color-primary;
  //   overflow-y: scroll;
  scroll-behavior: smooth;
  //   background: linear-gradient(#9fb8ad, $color-primary);
  //   background-color: darken($color: $color-primary, $amount: 20);
  background-color: $gray;
  height: 100vh;
  transition: 500ms ease-in-out;
  // animation: togglemenu 1s ;
  position: fixed;
  width: 20%;
  //   width: 300px;
  // border: 1px solid blue;
  z-index: 20;

  &.active {
    position: fixed;
    left: 0;
    top: 0;
    width: 80%;
    display: flex;
    // overflow: scroll;
    // scroll-snap-type: none;
    height: 100%;
    // background-color: #fff;
    &::before {
      content: '';
      width: 100%;
      height: 100%;
      background-color: $background-primary;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &.active-mini-menu {
    width: 80px;
    background-color: $background-primary;
  }

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position: relative;
    width: 100%;
    height: 100%;
    .navegation {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      overflow-y: auto;
      height: 100%;
      overflow-x: hidden;
      width: 100%;
      &-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
        width: 100%;
        // background-color: #fff;
        // margin: auto;
        i {
          min-width: 80px;
          font-size: 20px;
          color: #fff;
          text-align: center;
          line-height: 60px;
          transition: 500ms;
          height: 60px;
          cursor: pointer;
        }
        .img {
          cursor: pointer;
          text-align: left;
          // margin-right: 1rem;
          // padding: 0.3rem;
          // width: 30px;
          // height: 30px;
          // border-radius: 50%;
          // gap: 10px;
          box-shadow: $shadow;
          // background-color: darken($color: $background-primary, $amount: 10);
          background-color: $color-primary;

          // color: #fff;
          // display: flex;
          // justify-content: space-around;
          // margin-left: 10px;
          // align-items: center;
          border-radius: 100px;
          padding: 6px;
          // padding: 0.7rem;
          img {
            // width: 100%;
            // padding: 10px;
          }
        }
      }
    }
    .toggleMenu {
      position: absolute;

      width: 40px;
      height: 40px;
      background: $color-primary;
      border: 5px solid white;
      border-radius: 50%;
      right: -30px;

      top: calc(50% - 30px);
      cursor: pointer;
      &::before {
        content: '\f053';
        font-family: fontAwesome;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        width: 100%;
        font-size: 25px;
        color: white;
      }
      &.active::before {
        content: '\f054';
      }
    }
  }
  ul,
  .navegation-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    width: 100%;
    padding: 0;
    margin: 0;

    li {
      //   margin-bottom: 10px;
      display: flex;
      justify-content: center;
      border-bottom: 0.2px solid rgba(0, 0, 0, 0.4);
      //   padding: 10px 0px;
      overflow: hidden;
      width: 100%;
      align-items: center;
      //   transition: 500ms ease-in-out;
      // &.active {
      // 	background-color: darken($color: $gray, $amount: 4);
      // 	.icon {
      // 		color: $color-primary;
      // 	}
      // }
      &:hover {
        background-color: darken($color: $gray, $amount: 4);
        .icon {
          color: $color-primary;
        }

        // opacity: 0.9;
        // border-bottom: 0.5px solid white ;
      }
      a {
        position: relative;
        text-align: center;
        font-size: 20px;
        font-weight: 200;
        font-family: 'Rubik';
        // text-align: left;
        // margin-left: 1rem;
        display: flex;
        // justify-content: space-around;
        // align-items: center;
        width: 100%;

        .icon {
          position: relative;
          display: block;
          min-width: 80px;
          font-size: 20px;
          //   color: $color-primary;
          color: white;
          //   margin-right: 15px;
          text-align: center;
          line-height: 60px;
          transition: 500ms;
          height: 60px;
        }
        .title {
          line-height: 60px;
          height: 60px;
          color: white;
          text-align: left;
        }
      }
    }
  }
  &__footer {
    display: flex;
    justify-content: center;
    // gap: 10px;
    padding: 0px 0px;
    // background-color: #fff;
    width: 100%;
    text-align: center;
    a {
      width: 100%;
      padding: 10px 0px;
      background-color: darken($color: $gray, $amount: 6);
      i {
        margin-left: 10px;
      }
    }
  }
}

.cartel {
  position: absolute;
  background: #ffffff;
  width: auto;
  padding: 5px 14px;
  // max-width: 190px;
  width: max-content;
  text-align: center;
  color: $background-primary;
  box-shadow: $shadow;
  left: 5rem;
  z-index: 100000;
  display: none;
  border: 0.3px solid rgb(231, 231, 231);
  transition: 500ms ease-in-out;
  // border-radius: 4px;
  &::before {
    content: '';
    width: 15px;
    height: 15px;
    transform: rotate(45deg);
    background: #fff;
    // background-color: darken($color: $gray, $amount: 4);
    transform: translate(10%, 25%) rotate(45deg);
    position: absolute;
    left: -7px;
    transition: 500ms ease-in-out;
  }
}

.navbar.active-mini-menu {
  ul li:hover {
    .cartel {
      display: block;
    }
  }
}
#btn-close-mobil {
  display: none;
  align-self: flex-end;
  padding: 10px 10px 0 0;
  color: white;
  font-size: 30px;
  cursor: pointer;
  //   transition: 300ms ease-in-out;
  justify-content: center;
  margin-right: 2rem;
  // width: 30px;
  // height: 30px;
  align-items: center;
  border-radius: 50%;
  &:hover {
    // background-color: $background-primary;
    opacity: 0.7;
  }
}
#btn-open-menu-mobile {
  display: none;
}

// content header
.content-header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: calc(100% - 80px);
  padding: 0px 0px;
  background-color: $background-primary;
  position: fixed;
  //   transition: 500ms;
  z-index: 10;
  i {
    padding: 10px;
    font-size: 20px;
    cursor: pointer;
    color: $color-primary;
  }
  .content-header-right {
    position: relative;
    .user-logo {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0px 13px;
      cursor: pointer;
      span {
        color: #fff;
        text-transform: capitalize;
        font-size: 16px;
      }

      img {
        width: 50px;
        padding: 10px;
      }
    }
    .menu-user {
      visibility: hidden;
      opacity: 0;
      position: absolute;
      width: 200px;
      height: auto;
      background: white;
      border: 0.3px solid rgb(230, 230, 230);
      // box-shadow: 1px 2px 10px rgb(201, 201, 201);
      box-shadow: $shadow;
      right: 5px;
      top: 50px;
      // border-radius: 6px;
      z-index: 2000;
      // padding: 10px;
      &.active-mobile {
        visibility: visible;
        opacity: 1;
      }
      &::before {
        content: '';
        width: 20px;
        height: 20px;
        background-color: white;
        position: absolute;
        left: 50%;
        top: -6px;
        transform: rotate(45deg);
      }
      ul {
        padding: 0;
        margin: 0;
        width: 100%;
        li {
          // border-bottom: 0.4px solid rgb(233, 233, 233);
          // transition: 500ms ease-in-out;
          width: 100%;
          &:hover {
            background-color: rgb(245, 243, 243);
            a {
              color: $color-primary;
              i {
                color: $color-primary;
              }
            }
          }
          a {
            display: flex;
            align-items: center;
            i {
              color: $background-primary;
            }
          }
        }
      }
    }
  }
}

@keyframes togglemenu {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateX(200px);
  }
}

@media (max-width: 500px) {
  .toggleMenu {
    display: none;
  }
  .navbar {
    display: none;
  }
  .content-principal {
    width: 100% !important;
    margin: 0 !important;
  }
  .taller {
    display: block;
  }
  #btn-close-mobil {
    display: flex;
  }
  #btn-open-menu-mobile {
    display: block;
  }
  .content-header {
    justify-content: space-between;
    width: 100%;
  }
  .navbar__container .navegation {
    overflow-y: scroll;
  }
  .box-input-filter {
    flex-direction: column;
    // padding: 10px;
  }
  .search-histories {
    padding: 10px;
  }
  .menu-user {
    visibility: hidden !important;
    opacity: 0 !important;
  }
  .menu-user.active-mobile {
    visibility: visible !important;
    opacity: 1 !important;
  }
}

.link-lateral.active,
li.active {
  background: $background-primary;
}
